import React, {Component} from "react";
import PropTypes from 'prop-types';
import {connect} from "react-redux"
import {OraButton} from "eshop/components/OraCommonComponents";
import {goBackEmptyCart, removeFromCart, createNewCart} from "eshop/modules/cart/actions/cart";
import {HideWhenCartIsEmpty, ShowWhenCartIsEmpty} from "eshop/modules/cart/components/Utils";

const CheckoutActionsComponent = (props) =>
    <div>
        <ShowWhenCartIsEmpty>
            <OraButton className="" size="small" type="text" onClick={props.onClickReturn}>
                {props.descriptions.returnToShopButton || "Wróć do sklepu"}
            </OraButton>
        </ShowWhenCartIsEmpty>
        {props.showNewButton !== 'true' ?
            renderWithoutNewButton(props)
            :
            renderWithNewButton(props)
        }
    </div>
    ;

const renderWithoutNewButton = (props) =>
    <HideWhenCartIsEmpty className="l-row u-small-padding-top-l">
        <div className="l-col l-col-small-6">
            <OraButton className="" size="small" type="text" onClick={props.onClickReturn}>
                {props.descriptions.returnToShopButton || "Wróć do sklepu"}
            </OraButton>
        </div>

        <div className="l-col l-col-small-6 u-text-right">
            <OraButton size="small" type="text" onClick={props.onClickRemove}>
                {props.descriptions.removeCartButton || "Wyczyść koszyk"}
            </OraButton>
        </div>
    </HideWhenCartIsEmpty>
    ;

const renderWithNewButton = (props) =>
    <HideWhenCartIsEmpty>
        <div className="l-row u-padding-left-l u-padding-right-m u-padding-top-s u-padding-l">
            <div className={"l-col u-vertical-middle l-col-small-" + (props.showNewButton == 'true' ? "4" : "6")}>
                <OraButton className="" size="small" type="text" onClick={props.onClickReturn}>
                    {props.descriptions.returnToShopButton || "Wróć do sklepu"}
                </OraButton>
            </div>
            <div className={"l-col u-vertical-middle l-col-small-" + (props.showNewButton == 'true' ? "4" : "6")}>
                <OraButton size="small" type="text" onClick={props.onClickRemove}>
                    {props.descriptions.removeCartButton || "Wyczyść koszyk"}
                </OraButton>
            </div>
            {props.showNewButton == 'true'  &&
                [
                    <div className="l-col l-col-small-4 u-vertical-middle">
                        <OraButton size="small" type="text" onClick={props.onClickNew}>
                            {props.descriptions.newCartButton || "Nowy koszyk"}
                        </OraButton>
                    </div>
                ]
            }
        </div>
    </HideWhenCartIsEmpty>
    ;

CheckoutActionsComponent.propTypes = {
    onClickRemove: PropTypes.func
}

const mapDispatchToProps = dispatch => ({
    onClickRemove: () => dispatch(removeFromCart(null)),
    onClickReturn: () => dispatch(goBackEmptyCart()),
    onClickNew: () => dispatch(createNewCart())
})


export default connect(
                   null,
                   mapDispatchToProps
               )(CheckoutActionsComponent);
